
import React, { useEffect }from 'react'
import Vestibular from '../../Assets/vestibular.jpg'
import Medio from '../../Assets/medio.jpg'
import Fundamental from '../../Assets/fundamental.png'
import Aos from 'aos'
import "aos/dist/aos.css"
import '../Segmentos/segmentos.css'



function Segmentos (){
   

    useEffect(()=>{
        Aos.init({duration:3000})
      },[])  
   
    return (

        <div >
       
         <div className="container  " data-aos="fade-right">
                <h1 className='titulo'>
                    Metodologia - Fundamental ( I e II )
                </h1>
             <div className='area '>
                <img className='mt-5 img' src={Fundamental} alt="crianas na creche"/>
                <p  className='mt-5  texto ' >
                Princpios como o dilogo, a colaborao, o pensamento crtico, a criatividade e a pesquisa esto na base do trabalho desenvolvido no Ensino Fundamental I. Nesse segmento, diferentes metodologias e estratgias de ensino so usadas para ampliar o repertrio dos alunos e desenvolver diversas habilidades e competncias.
                Desde pequenos, nossos alunos so incentivados a construrem a prpria autonomia e a exercerem a autoria de suas produes.Desenvolvemos uma educao de alto nvel a partir das transformaes da sociedade com o ensino terico e prtico para o aluno se desenvolver atravs de sua prpria vivncia. O Ensino Fundamental II acompanha os alunos em uma fase de mudanas, com singularidades, formaes identitrias e culturais prprias e desenvolvimento das responsabilidades.
                </p>
             </div>
        </div>
        <div className="container " data-aos="fade-left" >
             <h1 className='titulo'>
                 Metodologia - Ensino Medio
             </h1>
             <div className='area '>
             <img className='mt-5  img' src={Medio}  alt="Crianas no ensino medio"/>
             <p className='mt-5 texto' >
             O nosso Ensino Mdio  construdo a partir de uma formao produtiva e responsvel com foco no desenvolvimento do autoconhecimento e do potencial do aluno. Valorizamos as experincias individuais e coletivas para que cada estudante se torne um indivduo crtico, participativo e dialgico, aplicando seus conhecimentos na resoluo de problemas de forma autnoma e proativa. Com um corpo de professores altamente qualificado, propostas interdisciplinares, metodologias ativas e formao tecnolgica, preparamos nossos alunos para enfrentar os grandes desafios da vida, do pleno exerccio da cidadania e do mundo do trabalho.
             </p>
            </div>
        </div>
        <div className="container " data-aos="fade-right" >
             <h1 className='titulo'>
                 Metodologia - Vestibular
             </h1>
             <div className='area '>
             <img className='mt-5 img' src={Vestibular} alt="cursinho para vestibular"/>
             <p  className='mt-5 texto'>
             {/* Com o objetivo de preparar os alunos para as provas de ingresso no Ensino Superior mais importantes do pas, desenvolvemos diferentes metodologias, projetos e simulados. Alm disso, temos um Coordenador de Vestibular que acompanha os estudantes ao longo do ano e os auxilia nos estudos e preparao para o Enem e vestibulares. */}
             </p>
            </div>
         </div>
        </div>
    )
}

export default Segmentos